.single-article {
	--carousel-width: 56.1em;
	font-size: 1rem;
	display: grid;
	grid-template-columns: var(--carousel-width) auto;
	column-gap: 4.4em;
	// column-gap: 3.9em;

	@media (max-width: $tablet) {
		grid-template-columns: 100%;
		row-gap: 4.3em;
	}
}

.single-article--reverse {
	@media (min-width: $tablet + 1) {
		grid-template-columns: 32em var(--carousel-width);
	}
	@media (max-width: $tablet) {
		grid-template-columns: 100%;
	}
}

.single-article-carousel {
	position: relative;
}

.single-article-carousel__buttons {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-left: 2.5em;
	padding-right: 2.5em;

	@media (max-width: $tablet) {
		padding-left: 0;
		padding-right: 0;
	}
}

.single-article-carousel__swiper {
	margin-bottom: 2.4em;

	@media (max-width: $tablet) {
		margin-bottom: 3em;
	}
}

.single-article-carousel__button-prev,
.single-article-carousel__button-next {
	display: flex;
	justify-content: center;
	align-items: center;
  left: unset;
  right: unset;
  top: unset;
  bottom: unset;
	position: relative;
	--size: 3em;
	--width: var(--size);
	--height: var(--size);
	width: var(--width);
	min-width: var(--width);
	max-width: var(--width);
	height: var(--height);
	min-height: var(--height);
	max-height: var(--height);
	background: #fff;
	border-radius: var(--size);
	margin: 0;
  transition: all 0.3s ease;
}

.single-article-carousel__button-prev::before,
.single-article-carousel__button-next::before {
  content: '';
  position: absolute;
  left: -5%;
  top: -5%;
  width: 110%;
  height: 110%;
  border-radius: 9999em;
  background-color: var(--c-secondary);
  transition: all 0.3s ease;
  transform: scale(0.1);
  opacity: 0;
}
.single-article-carousel__button-prev:hover::before,
.single-article-carousel__button-next:hover::before {
  @media (any-hover: hover) {
    opacity: 1;
    transform: scale(1);
  }
}

.single-article-carousel__button-prev::after,
.single-article-carousel__button-next::after {
	content: "";
	display: block;
	width: 80%;
	height: 80%;
	background-image: url("../img/common.insm/arrow-right.svg");
  transition: all 0.3s ease;
}

.single-article-carousel__button-prev::after {
	transform: scaleX(-1);
}

.single-article-carousel__button-prev:hover::after,
.single-article-carousel__button-next:hover::after {
  @media (any-hover: hover) {
    filter: brightness(0) invert(1);
  }
}

.single-article-carousel__media {
	border-radius: 5em;
	height: 31.5em;

	@media (max-width: $mobile) {
		height: 26em;
		border-radius: 3.2em;
	}
}

.single-article-carousel__media-pic {
	border-radius: inherit;
	display: block;
	width: 100%;
	height: 100%;
}

.single-article-carousel__media-img {
	border-radius: inherit;
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.single-article__content {
	max-width: 32rem;
	margin-bottom: 1.8em;

	@media (max-width: $tablet) and (min-width: $mobile + 1)  {
		max-width: 100%;
	}
  @media (min-width: $tablet + 1) {
      max-height: 30rem;
      overflow: auto;
      @include scrollbar;
      padding-right: 0.5em;
  }
  @media (max-width: $tablet) {
      max-width: 100%;
  }
}

.single-article__body {
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	flex-direction: column;
}

.single-article--reverse .single-article__body {
	width: 100%;

	@media (min-width: $tablet + 1) {
		order: -1;
	}
}

.single-article__title {
	font-size: 1.4em;
	font-weight: 600;
	margin-bottom: 1.6em;
	text-transform: uppercase;
	line-height: 1.2;
}
.single-article__title--big {
	font-size: 1.8em;
	margin-bottom: 1.2em;
}

.single-article__buttons {
	margin-top: auto;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin-bottom: -0.6em;

	@media (max-width: $tablet) {
		width: 100%;
		padding-right: 2em;
	}
}


.single-article__button {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin-right: 3.7em;

	@media (max-width: $tablet) {
		margin-right: 4em;
	}
}

.single-article__button:last-child {
	margin-right: 0;
}

.single-article__button-icon {
	display: block;
	--size: 4.2em;
	--width: var(--size);
	--height: var(--size);
	width: var(--width);
	min-width: var(--width);
	max-width: var(--width);
	height: var(--height);
	min-height: var(--height);
	max-height: var(--height);
	padding: 0.9em;
  transition: all 0.3s ease;

	@media (max-width: $tablet) {
		--size: 3.2em;
		padding: 0.7em;
	}
}

.single-article__button:hover .single-article__button-icon {
  @media (any-hover: hover) {
    filter: brightness(0) invert(1);
  }

}

.single-article__button-wrap {
  position: relative;
  --size: 4.2em;
  border-radius: var(--size);
  background: #fff;
  transition: all 0.3s ease;
  margin-right: 2em;
  overflow: hidden;

  @media (max-width: $tablet) {
    --size: 3.2em;
  }
}

.single-article__button-wrap::before {
  content: '';
  position: absolute;
  left: -5%;
  top: -5%;
  width: 110%;
  height: 110%;
  border-radius: 9999em;
  background-color: var(--c-secondary);
  transition: all 0.3s ease;
  transform: scale(0.1);
  opacity: 0;
}

.single-article__button:hover .single-article__button-wrap::before {
  @media (any-hover: hover) {
    opacity: 1;
    transform: scale(1);
  }
}

.single-article__button-text {
	text-transform: uppercase;
	font-size: 1.8em;
	font-weight: 600;
}
